import React from 'react';
import { Link } from "react-router-dom";
import { MDBFooter, MDBContainer, MDBRow, MDBCol, MDBIcon } from 'mdb-react-ui-kit';
import "./styles.css"

const Footer = () => {

    return (
        <>
        <MDBFooter bgColor='light' className='text-center text-lg-start text-muted'>
      <section className='d-flex justify-content-center justify-content-lg-between p-4 border-bottom'>
        <div className='me-5 d-none d-lg-block'>
          <span>También puedes encontrarnos en estas redes:</span>
        </div>

        <div>
          <a href='' className='me-4 text-reset'>
            <MDBIcon color='secondary' fab icon='facebook-f' />
          </a>
          <a href='' className='me-4 text-reset'>
            <MDBIcon color='secondary' fab icon='twitter' />
          </a>
          <a href='' className='me-4 text-reset'>
            <MDBIcon color='secondary' fab icon='google' />
          </a>
          <a href='' className='me-4 text-reset'>
            <MDBIcon color='secondary' fab icon='instagram' />
          </a>
        </div>
      </section>

      <section className=''>
        <MDBContainer className='text-center text-md-start mt-5'>
          <MDBRow className='mt-3'>
            <MDBCol md='3' lg='4' xl='3' className='mx-auto mb-4'>
              <h6 className='text-uppercase fw-bold mb-4'>
                <MDBIcon color='secondary' icon='gem' className='me-3' />
                Señor Tornillero
              </h6>
              <p>
              Nuestra amplia selección de tornillos de alta calidad está diseñada para satisfacer todas tus necesidades, desde pequeñas reparaciones hasta grandes construcciones.
              </p>
            </MDBCol>

            <MDBCol md='2' lg='2' xl='2' className='mx-auto mb-4'>
              <h6 className='text-uppercase fw-bold mb-4'>Productos</h6>
              <p>
                <Link to='tienda' className='text-reset'>
                  Tornillos
                </Link>
              </p>
              <p>
                <Link to='tienda' className='text-reset'>
                  Varillas
                </Link>
              </p>
              <p>
                <Link to='tienda' className='text-reset'>
                  Tuercas
                </Link>
              </p>
              <p>
                <Link to='tienda' className='text-reset'>
                  Ver Todo
                </Link>
              </p>
            </MDBCol>

            <MDBCol md='3' lg='2' xl='2' className='mx-auto mb-4'>
              <h6 className='text-uppercase fw-bold mb-4'>Navegación</h6>
              <p>
                <Link to='tienda' className='text-reset'>
                  Tienda
                </Link>
              </p>
              <p>
                <Link to='mayoristas' className='text-reset'>
                  Mayoristas
                </Link>
              </p>
              <p>
                <Link to='catalogo' className='text-reset'>
                  Catálogo
                </Link>
              </p>
              <p>
                <Link to='nosotros' className='text-reset'>
                  Nosotros
                </Link>
              </p>
            </MDBCol>

            <MDBCol md='4' lg='3' xl='3' className='mx-auto mb-md-0 mb-4'>
              <h6 className='text-uppercase fw-bold mb-4'>Contacto</h6>
              <p>
                <MDBIcon color='secondary' icon='home' className='me-2' />
                Qro, 10012, México
              </p>
              <p>
                <MDBIcon color='secondary' icon='envelope' className='me-3' />
                info@mail.com
              </p>
              <p>
                <MDBIcon color='secondary' icon='phone' className='me-3' />+52(222) 222-2222
              </p>
            </MDBCol>
          </MDBRow>
        </MDBContainer>
      </section>

      <div className='text-center p-4' style={{ backgroundColor: 'rgba(0, 0, 0, 0.05)' }}>
        © 2023 Copyright: 
        <a className='text-reset fw-bold px-1' href=''>
          Señor Tornillero
        </a>
      </div>
    </MDBFooter>
        </>
    )
}

export default Footer;